
import CONTRACTHELP from './contracthelp.js'
import store from '@/store/index.js'
const trxNftStandard = {};
let currentContract=null; 
//获取当前合约实例

//获取当前合约实例
trxNftStandard.getCurrentContract=()=>{
    if(window.tronWeb&&currentContract==null) {
        currentContract=(CONTRACTHELP.trx721StandarContract()).methods; 
    }
    return currentContract
}


//查看公开销售是否已开始
trxNftStandard.totalSupply = async function(){
    let totalSupply= await currentContract.totalSupply().call();
    return parseInt(totalSupply._hex,16);
}; 
//铸造
trxNftStandard.publicMint = async function(mintNumber,mintPrice,callback,errorCallBack){

    let result ={};
    //使用send来执行智能合约方法，消耗资源并且还广播到网络。
	//feeLimit	调用合约方法消耗最大数量的SUN。上限是 1000 TRX。(1TRX = 1,000,000SUN)	Integer
    //callValue	本次调用往合约转账的SUN。	Integer callValue: this.tronWeb.toHex(25 * Math.pow(10,6)),//发送TRX余额
    //shouldPollResponse 如果设置为 TRUE，则会等到在 Solidity 节点上确认事务之后再返回结果。 Boolean
    //tokenId	本次调用往合约中转账TRC10的tokenId。如果没有，不需要设置	String
    //tokenValue	本次调用往合约中转账TRC10的数量，如果不设置tokenId，这项不设置。	Integer
    const mintAmount = mintNumber*mintPrice;
    const value = window.tronWeb.toHex(mintAmount * Math.pow(10,6));
    let res = await currentContract["mint"](mintNumber);
    res.send({feeLimit: 100000000,callValue: value,shouldPollResponse: false}).then(
        res => { callback(res) },
		err => { errorCallBack(err) }
    );
}
export default trxNftStandard