

import trx721StandarAbi from '../config/abi/trx721Standard.json'
import contractAddress from '../config/constants/constants.js'
const CONTRACTHELP = {};

//创建合约实例  let tronWeb = window.tronWeb
const getContract = (contractABI, contractAddress) => {
    
    return window.tronWeb.contract(contractABI, contractAddress);  
};
//合约实例721
CONTRACTHELP.trx721StandarContract = () => {
    return getContract(trx721StandarAbi, contractAddress.trx721StandardAddress);
}
export default CONTRACTHELP