import store from '@/store/index.js'

const CONNECT = {};

let connectResult = {
    state:false,
    balance:0,
};
//链接钱包
CONNECT.linkWallet = async function() {
    if(window.tronWeb) {
        let tronWeb = window.tronWeb;
        if (!tronWeb.defaultAddress.base58) {
            connectResult.msg = 'Please login wallet!'
        } else {
            let fullNode = tronWeb.fullNode.host;
            let suportNode = store.state.network[store.state.supportData];
            if(fullNode==suportNode.rpcUrls){
                connectResult.state = true;
                connectResult.address = tronWeb.defaultAddress.base58;
                var balance = await tronWeb.trx.getBalance(tronWeb.defaultAddress.base58);
                connectResult.balance = tronWeb.fromSun(balance);
            }else{
                connectResult.msg = 'The current network is not supported. Please switch the network to'+suportNode.chainName +'and try again！';
            }
        }

    } else {
        connectResult.msg = 'Please install the wallet!'
    }
    return connectResult
}
//查询钱包余额
CONNECT.getbalance = async function(walletAddress) {
    let balance = tronWeb.fromSun(await tronWeb.trx.getBalance(walletAddress));
    return balance
}



export default CONNECT